import React, { useEffect, useState } from 'react'
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import styles from './styles.module.css'
import ModalCopied from '../../../../ui/modalCopied/ModalCopied';
import { useAppSelector } from '../../../../hooks/redux-hooks';
import CoinModal from '../CoinModal/CoinModal';
const CoinName = () => {
    const coin=useAppSelector(state=>state.coin)
    const [isActiveModal, setIsActiveModal]=useState<boolean>(false)
    const [active, setActive]=useState<boolean>(false)
    const copyText=()=>{
      navigator.clipboard.writeText(coin)
      setIsActiveModal(true)
      setTimeout(()=>{
        setIsActiveModal(false)
      }, 1000)
    }
   
  return (
    <div className={styles.wrap}>
      {coin=='' ? (
        <div className={styles.container}> 
          <div className={styles.obv} onClick={()=>setActive(!active)}>
            <p className={active ? styles.activeChooseCoin :styles.chooseCoin}> </p>
          </div>
        </div>
      ) : (
        <div className={styles.container}>
          <div className={styles.obv}>
            <div className={styles.choose}>
              <p onClick={()=>setActive(!active)} className={active ? styles.activeChooseCoin :styles.chooseCoin}>{coin}</p>
              <ContentCopyIcon onClick={copyText}/>
            </div>
            <ModalCopied isActiveModal={isActiveModal}/>
          </div>
        </div>
      )}
      {active && <CoinModal setActive={setActive} active={active}/>}
    </div>
  )
}

export default CoinName