import React, { RefObject, useEffect } from 'react'
import styles from './styles.module.css'
import { useAppDispatch, useAppSelector } from '../../../../hooks/redux-hooks';
import { setGraphicCoin, setGraphicDistance } from '../../../../pages/MainPage/slices/GraphicSlice';
import { changeDistance } from '../../../MenuSettings/slices/DistanceSetSlice';
import { ICoin, ICoinQuery } from '../../../../pages/MainPage/api/CoinApi'
import Loader from '../../../../components/Loading/Loader';
interface AllCoins{
    graphicRef: RefObject<HTMLDivElement>,
    requestingSymbols: ICoin[],
    data:ICoin[] | undefined,
    searchingLoading: boolean,
    text: string,
    id: number
}
const AllCoins:React.FC<AllCoins> = ({graphicRef,requestingSymbols,data,searchingLoading,text,id }) => {
    const graphic=useAppSelector(state=>state.graphics.find(item=>item.id===id))
    const dispatch=useAppDispatch()
    const chooseCoin=(id:number, coin: string, type: string | undefined)=>{
        if(graphic?.distance==='0'){
            dispatch(changeDistance(''))
            dispatch(setGraphicDistance(''))
        }
        dispatch(setGraphicCoin({id:id,coin:coin, type: type}))
    }
    return (
    <div className={styles.coins} style={{maxHeight:graphicRef.current?.clientHeight!==undefined ? graphicRef.current?.clientHeight-49 : '0' }}>
             <div className={styles.coinsName}>
                {requestingSymbols.length==0 && text.length==0 ? (
                    <>
                    {data?.map((sym:ICoin,index:number)=>(
                        <div key={index} className={styles.symbol} onClick={()=>chooseCoin(id, sym.symbol, sym.permissions==undefined ? 'futures' : 'spot')}>
                            <p>{sym.symbol}</p>
                            <p className={sym.permissions==undefined ? styles.futures : styles.spot}>{ sym.permissions==undefined ? 'futures' : 'spot'}</p>
                        </div>
                    ))}
                    </>
                ) : (
                    <>
                    {searchingLoading ? (<Loader/>) : requestingSymbols.map((sym:ICoin,index:number)=>(
                        <div key={index} className={styles.symbol} onClick={()=>chooseCoin(id, sym.symbol,  sym.permissions==undefined ? 'futures' : 'spot')}>
                            <p>{sym.symbol}</p>
                            <p className={sym.permissions==undefined ? styles.futures : styles.spot}>{ sym.permissions==undefined ? 'futures' : 'spot'}</p>
                        </div>
                    ))}
                    </>
                )}
                {text.length!==0 && !searchingLoading && requestingSymbols.length==0 && <p className={styles.nothing}>  !</p>}
             </div>
        </div>
  )
}

export default AllCoins