import { createSlice } from '@reduxjs/toolkit';
const initialState=''
const CoinSlice = createSlice({
    name: 'coin',
    initialState,
    reducers: {
        changeCoin(state,action){
            return action.payload 
        } 
    },
});

export const {changeCoin} = CoinSlice.actions;

export default CoinSlice.reducer;