import React, { ChangeEvent, useEffect, useState, useRef,FC, RefObject } from 'react'
import styles from './styles.module.css'
import { ICoin, useGetCoinQuery } from '../../../../pages/MainPage/api/CoinApi';
import { useAppDispatch, useAppSelector } from '../../../../hooks/redux-hooks';
import useOutsideClick from '../../../../hooks/useOutClick';
import { type } from 'os';
import AllCoins from '../AllCoins/AllCoins';
interface ICoinList{
    active: boolean,
    setActive:(arg:boolean)=>void,
    id: number,
    graphicRef: RefObject<HTMLDivElement>
}
const CoinList:React.FC<ICoinList> = ({setActive, active,id, graphicRef}) => {
    const coins=useAppSelector(state=>state.coins)
    const [searchingLoading, setSearchingLoading]=useState<boolean>(false)
    const ref=useRef<HTMLDivElement | null>(null)
    const [requestingSymbols, setRequestingSymbols]=useState<ICoin[]>([])
    const [text, setText]=useState<string>('')
    useEffect(()=>{
        if(!text){setRequestingSymbols([])}
        if(coins){
            const copyarr:ICoin[]=[]
            setSearchingLoading(true)
            coins?.forEach((symbol)=>{
                if(symbol.symbol.includes(text.toUpperCase())){
                    copyarr.push(symbol)
                }
            })
            setRequestingSymbols(copyarr)
            setSearchingLoading(false)
        }    
    },[text])
    const onClose=()=>{
        setActive(false)
    }
    useOutsideClick(ref, onClose,active)
  return (
    <div className={styles.wrap} ref={ref}>
        <div className={styles.coiIinp}>
            <input id='preset_input' type="text" value={text} onChange={(e:ChangeEvent<HTMLInputElement>)=>setText(e.target.value)} placeholder=' '/>
        </div>
        <AllCoins requestingSymbols={requestingSymbols} graphicRef={graphicRef} data={coins} searchingLoading={searchingLoading} text={text} id={id}/>
    </div>
  )
}

export default CoinList