import React,{useRef, useState, useEffect} from 'react'
import styles from './styles.module.css'
import useOutsideClick from '../../../../hooks/useOutClick'
import CloseRoundedIcon from '@mui/icons-material/CloseRounded';
import { useGetCoinQuery } from '../../../../pages/MainPage/api/CoinApi';
import CoinSetting from '../CoinSetting/CoinSetting';
import { ICoin, ICoinQuery } from '../../../../pages/MainPage/api/CoinApi'
import Loader from '../../../../components/Loading/Loader';
import CoinModalFooter from '../CoinModalFooter/CoinModalFooter';
import { useAppSelector } from '../../../../hooks/redux-hooks';
interface ICoinModal{
  active:boolean,
  setActive: (arg: boolean)=>void
}
const CoinModal:React.FC<ICoinModal> = ({setActive, active}) => {
  const coins=useAppSelector(state=>state.coins)
  const [filtered, setFiltered]=useState<ICoin[] | undefined>(coins)
  const [text, setText]=useState<string>('')
  const ref=useRef<HTMLDivElement | null>(null)
//   useEffect(()=>{
//     if(!text){setFiltered(data)}
//     if(data){
//         const copyarr:ICoin[]=[]
//         data?.forEach((symbol)=>{
//             if(symbol.symbol.includes(text.toUpperCase())){
//                 copyarr.push(symbol)
//             }
//         })
//         setFiltered(copyarr) 
//     }    
// },[text])
  const onClose=()=>{
    setActive(false)
  }
  useOutsideClick(ref,onClose, active)
  return (
    <div className={styles.blur}>
        <div className={styles.wrap} ref={ref}>
            <div className={styles.header}>
                <p>  24 </p>
                <div className={styles.close} onClick={onClose}>
                  <CloseRoundedIcon/>
                </div>
            </div>
            <div className={styles.input}>
              <input type="text" value={text} onChange={(e:React.ChangeEvent<HTMLInputElement>)=>setText(e.target.value)} placeholder=' '/>
            </div>
            <div className={styles.coinsWrap}>
              <div className={styles.coins}>
                {coins?.map((item:ICoin, index: number)=>(
                  <CoinSetting key={index} symbol={item}/>
                ))}
              </div>
            </div>
          <div>
            <CoinModalFooter/>
          </div>
        </div>
    </div>
  )
}

export default CoinModal